import React from 'react';
import logo from './logo.svg';
import './App.css';
import Game from './Game';
import Details from './Details';
import Project from './project';
import { ImageProject } from './interfaces';
class App extends React.Component<{}, { currentFile: string; details: boolean }> {
	constructor(props: any) {
		super(props);
		this.state = {
			currentFile: '',
			details: false
		};
	}
	render() {
		let temp: any = Project;

		let games = Object.keys(temp).sort((a, b) => a.localeCompare(b)).map((fileName: string) => {
			return (
				<Game
					fileName={fileName}
					projects={temp[fileName]}
					project={temp}
					onClick={() => {
						this.setState({ currentFile: fileName, details: true });
					}}
				/>
			);
		});
		if (this.state.currentFile && this.state.details) {
			return (
				<div className="App">
					<Details
						onClick={() => {
							this.setState({ details: false });
						}}
						fileName={this.state.currentFile}
						projects={temp[this.state.currentFile]}
						project={temp}
					/>
				</div>
			);
		}
		return <div className="App">{games}</div>;
	}
}

export default App;
